.CurrenciesTableRow {
  cursor: pointer;
}

  .CurrenciesTableRow:hover {
    background-color: #EEE;
  }

  .CurrenciesTableRow > td {
    padding: 6px 4px;
  }

  .CurrenciesTableRow > td:first-child {
      padding-left: 10px;
    }

  .CurrenciesTableRow > td:last-child {
      padding-right: 10px;
    }

  .CurrenciesTableRow__index {
    width: 60px;
  }

  .CurrenciesTableRow__symbolCell {
    width: 100px;
  }

  .CurrenciesTableRow__symbol {
    padding: 4px 6px;
    background-color: #CCC;
    border-radius: 2px;
  }

  .CurrenciesTableRow__price {
    text-align: right;
  }

  .CurrenciesTableRow__change24h {
    width: 120px;
    text-align: right;
  }
