.CryptocurrencyDetailsPage {
  max-width: 900px;
  width: 100%;
  margin: 0 auto 40px;
  padding: 0 40px;
}

  .CryptocurrencyDetailsPage__name {
    font-size: 30px;
  }

  .CryptocurrencyDetailsPage__symbol {
    color: rgba(39, 52, 64, .5);
