.SettingsPage {
  max-width: 900px;
  width: 100%;
  margin: 0 auto 40px;
  padding: 0 40px;
}

  .SettingsPage__fiatCurrencyFieldset {
    display: -webkit-box;
    display: flex;
  }

  .SettingsPage__fiatCurrencyOption input {
      display: none;
    }

  .SettingsPage__fiatCurrencyOption input:checked + label {
      border: 1px solid #4bb543;
    }

  .SettingsPage__fiatCurrencyOption label {
      display: -webkit-box;
      display: flex;
      -webkit-box-align: center;
              align-items: center;
      margin-right: 20px;
      padding: 0 8px;
      border: 1px solid rgba(0, 0, 0, .1);
      border-radius: 6px;
      -webkit-transition: border .2s ease-out;
      transition: border .2s ease-out;
      cursor: pointer;
    }

  .SettingsPage__fiatCurrencyOption .Img {
      height: 40px;
      margin-right: 10px;
    }

  .SettingsPage__bottomBar {
    margin-top: 60px;
  }
